html {
	font-family: $fam-body;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
img {
	max-width: 100%;
	height: auto;
}
body {
	margin: 0;
	font-size: 14px;
	font-weight: 300;
  letter-spacing: .015em;
  line-height: 1.4;
}
*, *:before, *:after {
    box-sizing: border-box;
}

.cf:after {
	content: '';
	display: block;
	clear: both;
}


